import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { Settings } from 'src/app/entities/settings';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ulubione-page',
  templateUrl: './ulubione-page.page.html',
  styleUrls: ['./ulubione-page.page.scss'],
})

export class UlubionePagePage implements OnInit {
  
constructor(
  public settings: Settings,
  private route: ActivatedRoute,
) {
   document.documentElement.setAttribute("lang", this.settings.langCode);
}

  ngOnInit() {
    this.route.paramMap.subscribe((paramMap) => {
      this.settings.langCode = this.route.snapshot.paramMap.get("lang");
    });
  }

}